.input {
  position: relative;
  margin-bottom: 30px;
  display: flex;
  text-align: end;
  flex-wrap: wrap;
  justify-content: space-around;
  max-width: 600px;
  width: 100%;
  outline: none; }
  .input__block {
    font-size: 16px;
    padding: 10px;
    margin-bottom: 10px;
    display: block;
    max-width: 300px;
    width: 100%;
    margin-right: 20px;
    border: none;
    border-bottom: 1px solid #ccc; }
    .input__block:focus {
      outline: none; }
  .input__btn {
    padding: 5px 40px;
    border: none;
    outline: none;
    font-family: "Montserrat", sans-serif;
    font-weight: 700;
    color: white;
    background-color: #5264AE;
    transition: all .3s ease;
    cursor: pointer;
    border-radius: 5px; }
    .input__btn:hover {
      opacity: .8; }

.todo-item {
  font-size: 20px;
  border-bottom: 1px solid #5264AE;
  min-height: 60px;
  display: flex;
  align-items: center;
  list-style: none;
  padding: 0 16px 0 60px;
  cursor: pointer;
  color: #5264AE;
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: relative;
  transition: all .3s ease; }
  .todo-item:hover {
    opacity: .8; }

.text {
  font-family: "Montserrat", sans-serif; }

.completed {
  text-decoration: line-through; }

.checked {
  background-color: #5264AE; }

.mark {
  position: absolute;
  left: 20px;
  width: 20px;
  height: 20px;
  border: 1px solid #5264AE;
  border-radius: 50%; }

.close {
  position: relative;
  width: 20px;
  height: 20px; }
  .close:after {
    content: '';
    position: absolute;
    top: 0;
    left: 9px;
    height: 100%;
    width: 2px;
    transform: rotate(45deg);
    background-color: #5264AE; }
  .close:before {
    content: '';
    position: absolute;
    top: 0;
    right: 9px;
    height: 100%;
    width: 2px;
    transform: rotate(-45deg);
    background-color: #5264AE; }

.todo-list {
  margin: 0 auto;
  padding: 0;
  max-width: 700px;
  width: 100%; }

.wrapper {
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center; }

h1 {
  text-align: center;
  font-family: 'Montserrat', sans-serif;
  font-size: 40px;
  color: #5264AE; }


body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace; }

